*, :before, :after {
  box-sizing: border-box;
  user-select: none;
}

body {
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  display: flex;
}

.world {
  position: relative;
  overflow: hidden;
}

.score {
  font-size: 3vmin;
  position: absolute;
  top: 1vmin;
  right: 1vmin;
}

.start-screen {
  font-size: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hide {
  display: none;
}

.ground {
  --left: 0;
  width: 300%;
  bottom: 0;
  left: calc(var(--left) * 1%);
  position: absolute;
}

.dino {
  --bottom: 0;
  height: 30%;
  left: 1%;
  bottom: calc(var(--bottom) * 1%);
  position: absolute;
}

.cactus {
  left: calc(var(--left) * 1%);
  height: 16%;
  position: absolute;
  bottom: 0;
}
